* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
}

aside {
    display: flex;
    flex-direction: column;
    border: 1px solid rebeccapurple;
    width: 100%;
}

.sidebar-head {
    width: 100%;
    border: 1px solid green;
    height: 8vh;
}

.cross {
    width: 18%;
    height: 100%;
    background-color: aqua;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.cross-line {
    width: 32px;
    height: 10%;
    margin-left: 0.3rem;
    margin-bottom: 0.4rem;
    border-radius: 8px;
    background-color: black;
}

.cross-line-one {
    transform: rotate(40deg);
    position: relative;
    top: 0.7rem;
}

.cross-line-two {
    transform: rotate(-40deg);
}

.cross:hover {
    background-color: red;
}

.side-main {
    display: flex;
    flex-flow: column nowrap;
    min-height: 100%;
    width: 100%;
    border: 1px solid red;
}

.lets-sign {
    padding: 1rem;
    font-size: 1rem;
}

.lets-sign .lets {
    font-size: 2rem;
    font-weight: bold;
    font-family: fantasy;
    letter-spacing: 4px;
}

.login {
    display: flex;
    padding-left: 1em;
    column-gap: 1rem;
}

.side-menu {
    display: flex;
    column-gap: 10em;
    align-items: center;
    margin: 2em 1em 2em 1em;
    padding: 1em;
    background-color: gainsboro;
    border-radius: 1rem;
}

.side-deals {
    display: flex;
    column-gap: 10em;
    align-items: center;
    margin: 0px 1em 2em 1em;
    padding: 1em;
    background-color: gainsboro;
    border-radius: 1rem;
}

.side-others {
    display: flex;
    padding: 1em;
    background-color: gainsboro;
    flex-flow: column;
    align-items: center;
}

.side-others a {
    padding-bottom: 1.1em;
    text-decoration: none;
}

aside {
    display: none;
}

nav {
    display: flex;
    justify-content: space-around;
    min-height: 57px;
    align-items: center;
    position: relative;
}

li {
    list-style-type: none;
}

.vbfc {
    font-size: 2em;
    font-weight: bold;
    position: relative;
    left: 16px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.buckets {
    display: flex;
    align-items: center;
}

.rs {
    font-weight: bold;
}

.image {
    position: relative;
    left: 10px;
}

.menu-line {
    height: 3px;
    width: 23px;
    background-color: black;
    margin-bottom: 3px;
}

.menu:hover {
    cursor: pointer;
}

.menu:active {
    background-color: aqua;
}

.showmenu {
    display: block;
}

.hidemenu {
    display: none;
}

.nav {
    display: none;
}

aside {
    display: none;
}

.homebghead {
    background-color: aqua;
}